import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ListsComponent } from './lists/lists.component';
import { CardsComponent } from './cards/cards.component';
import { CarouselComponent } from './carousel/carousel.component';
import { SharedModule } from '../shared/shared.module';
import { RouterModule } from '@angular/router';
import { TabsetsComponent } from './tabsets/tabsets.component';

export const layoutsRoutes = [
  {
    path: '',
    redirectTo: 'carousel'
  },
  {
    path: 'lists',
    component: ListsComponent
  },
  {
    path: 'cards',
    component: CardsComponent
  },
  {
    path: 'carousel',
    component: CarouselComponent
  },
  {
    path: 'tabsets',
    component: TabsetsComponent
  }
];

@NgModule({
  declarations: [ListsComponent, CardsComponent, CarouselComponent, TabsetsComponent],
  imports: [
    CommonModule,
    SharedModule,
    RouterModule.forChild(layoutsRoutes)
  ]
})
export class LayoutsModule { }
